@charset 'utf-8';
@import 'rest.scss';
@import 'global.scss';

/* 
 * base.scss 
 * 框架基础样式
 * 公共样式库
*/

/* ===基础样式覆盖=== */
* {
  box-sizing: border-box;
}

body {
  font-family: $fonts;
  font-size: 13px;
}

// 默认链接样式
a {
  color: $Theme-color;
  text-decoration: none;
  &:hover {
    color: $Theme-color-light;
  }
  &.disabled {
    color: gainsboro;
    cursor: not-allowed;
    pointer-events: none;
  }
}

// 默认输入框样式
textarea,
input[type="text"],
input[type="file"],
input[type="number"],
input[type="password"] {
  @extend %ib;
  padding: 0 8px;
  border: 1px solid #d3d3d3;
  border-radius: 0.2em;
  font-size: inherit;
  // transition: 0.3s;
  &:focus {
    border-color: rgba($Theme-color-light, 0.5);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 7px rgba($Theme-color-light, 0.5);
  }
}

textarea {
  padding: 0.8em;
  min-height: 100px;
}


input[type="text"],
input[type="file"],
input[type="number"],
input[type="password"] {
  height: 2.3em;
  line-height: 2.3em;
  color: #333;
}

textarea.disabled,
input.disabled,
textarea.readonly,
input.readonly,
textarea[disabled],
textarea[readonly],
input[disabled],
input[readonly] {
  background-color: #EBEBE4 !important;
  pointer-events: none !important;
}

input.file-text[readonly] {
  background-color: #fff !important;
}

// 默认下拉列表样式
select {
  padding: 0 0.2em;
  border: 1px solid #d3d3d3;
  border-radius: 0.2em;
  // transition: 0.3s;
  height: 2.3em;
  font-size: inherit;
}





input[type="checkbox"] {
  vertical-align: middle;
}


/* ===公共样式=== */
.full-size {
  @extend %full-size;
}

.ib {
  @extend %ib;
}

.fix {
  @extend %fix;
}

.ellipsis {
  @extend %ellipsis;
}

.min-width {
  min-width: $min-width;
}
// 隐藏
.hidden {
  display: none !important;
}
// 移除隐藏
.over-hide {
  overflow: hidden;
}
// 内联居中
.ib.middle {
  @extend .ib;
  width: 0;
  height: 100%;
  vertical-align: middle;
}
// 绝对定位
.absolute {
  position: absolute;
  left: 0;
  top: 0;
}
// 修复iphone滚动条
.fix-iphone-scroll {
  -webkit-overflow-scrolling: touch;
}

// 禁止选中文本
.noselect {
  /* 在 IE < 10 和Opera < 15中我们需要在需要禁止选中的元素上面添加一个属性 unselectable="on" */
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently not supported by any browser */
}


.text-left {
  text-align: left;
}
