@charset 'utf-8';
@import '../base/global.scss';
@import '../base/base.scss';
/* 
 * config.scss
 * 管理主题色, 主文本色, 次文本色
 * 覆盖组件样式, 来达到特殊化
*/

/* ===主题管理=== */
// 背景-主色
.theme-bg {
  background-color: $Theme-color;
  &.hover {
    background-color: $Theme-color;
  }
}
// 文本-主色
.theme-color {
  color: $Theme-color;
  &.hover {
    color: $Theme-color-light;
  }
}
// 背景hover-亮主色
.theme-bg-hover:hover {
  background-color: $Theme-color-light;
}
// 背景-灰色
.bg-gray-dark {
  background-color: $Theme-gray-dark;
}
// 背景-灰色(中等强度)
.bg-gray-middle {
  background-color: $Theme-gray-middle;
}
// 背景-灰色(亮灰)
.bg-gray-light {
  background-color: $Theme-gray-light;
}
// 文本-灰色
.bg-gray-dark {
  color: $Theme-gray-dark;
}
// 文本-灰色(中等强度)
.bg-gray-middle {
  color: $Theme-gray-middle;
}
// 文本-灰色(亮灰)
.bg-gray-light {
  color: $Theme-gray-light;
}
// 文本-主要
.text-primary {
  color: $Theme-text-primary;
}
// 文本-次要
.text-secondary {
  color: $Theme-text-secondary;
}
// 文本-第三次要
.text-secondary {
  color: $Theme-text-third;
}



/* 覆盖组件样式 */
// 切换抽屉
.toggle-drawerNav {
  cursor: pointer;
}

// 表格
.data-table {
  font-size: inherit;
}
.data-table th {
  padding: 1em 1.5em 1em 1.5em;
}
.data-table td {
  padding: 0 1.5em;
}
.data-table th,
.data-table td {
  text-align: left;
  font-size: inherit;
}
// 表格换行
.data-table td.nextline {
  word-break: keep-all;
  white-space: normal;
}
// 表格换行2
.data-table.nextline tbody {
  white-space: initial;
  word-break: keep-all;
}
