// message组件
.message-wrap {
  font-size: inherit;
  padding: 0 1em;

  .message-content {
    padding: 1em 2em 1em 0.5em;
    border-radius: 0.3em;
  }

  .icon {
    font-size: 1.5em;
    margin-right: 0;
  }

}

// 对话框组件
.MyDialog-wrap .dialog-box {
  border-radius: 0.5em;
  min-width: 150px;

}
.MyDialog-wrap .dialog-header {
  font-size: inherit;
  .p-title {
    padding: 0.8em;
    font-size: 1.2em;
  }
  .close {
    font-size: inherit;
    right: 0.5em;
    top: 21%;
  }
}
.MyDialog-wrap .body {
  border-radius: 0.5em;
}
.Alert-footer {
  border-radius: 0 0 0.5em 0.5em;

  .btn-2 {
    font-size: 1.2em;
    line-height: 3em;
    height: 3em;
  }

  .btn-2.reverse {
    color: #FF5722;
  }
}

// SelectBox组件
select[enumstatus="false"] + .SelectBox {
  border-color: #F44336;
}
.SelectBox:focus {
  background-color: #eee;
}
// fix行高问题导致后续item挤换行
.item > .input-area .SelectBox {
  height: 30px;
  line-height: 30px;
}

// Toggle 组件
.toggle-checkbox .box_outline, .toggle-radio .box_outline {
  // border: 2px solid $Theme-color;
}
.toggle-checkbox[checked] .box_outline,
.toggle-radio[checked] .box_outline {
  border-color: $Theme-color;
}
.toggle-checkbox[checked] .tick_outline,
.toggle-radio[checked] .tick_outline {
  background-color: $Theme-color;
}
.toggle-checkbox[disabled] .box_outline,
.toggle-radio[disabled] .box_outline {
  border-color: #ddd;
}


.toggle-switch[checked] .track {
  background: rgba($Theme-color-light, 0.5);
}
.toggle-switch[checked] .thumb {
  background: $Theme-color;
}

// 验证提示
// 对话框提示便条
.dialog-note {
  display: block;
  padding: 10px 5px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #FFF2B5;
  box-shadow: 0 1px 5px rgba(0,0,0,0.2);
  text-align: center;
  width: 100%;

  .message {
    font-size: 12px;
    color: coral;
  }

  .close {
    position: absolute;
    right: 10px;
    top: 10px;
    color: #666;
    cursor: pointer;
  }
}

.vd-error,
.next-error {
  color: #F44336;
}

// 输入框异步状态
input.loading {
  background-color: #EBEBE4;
}

// input异步转圈
.input-loading-ico {
  position: absolute;
  top: 0;
  right: 0.3em;
  line-height: 2.3em;
  color: #F44336;
}

// 验证失败样式
[enumstatus="false"] {
  border: 1px solid #F44336 !important;
}

// drowdown表单提示
// 箭头左偏移
$arrow-offsetX: 10px;
// 箭头宽度
$arrow-width: 0.8em;
// 背景色
$tips-color: #FFF2B5;
// 边框尺寸
$border-size: 0.1em;

.pop-message {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: 500;
  border-radius: 0.3em;
  color: coral;
  background-color: $tips-color;
  border: 1px solid #ddd;
  background-clip: padding-box;
  // transition: opacity 0.3s;

  // 箭头
  &:after,
  &:before {
    position: absolute;
    display: block;
    content: "";
    width: 0;
    height: 0;
    border: $arrow-width dashed transparent;
    z-index: 1;
  }
}

// 上箭头
.down:after, .down:before {
  border-bottom-style: solid;
  border-width: 0 $arrow-width $arrow-width;
}
.up:after, .up:before {
  border-bottom-style: solid;
  border-width: $arrow-width $arrow-width 0;
}
.down.pop-message:before {
  border-bottom-color: #ddd;
  bottom: 0;
  left: $arrow-offsetX;
  top: -$arrow-width;
  pointer-events: none;
}

/** 下箭头 */
.up.pop-message {
  top: auto;
  bottom: 100%;
  margin: 0 0 ($arrow-width + $border-size);
}
.down.pop-message:after {
  border-bottom-color: $tips-color;
  bottom: -$border-size;
  top: -($arrow-width - $border-size);
  left: $arrow-offsetX;
}
.up.pop-message:before {
  border-top-color: #ddd;
  bottom: 0;
  left: $arrow-offsetX;
  bottom: -$arrow-width;
  pointer-events: none;
}
.up.pop-message:after {
  border-top-color: $tips-color;
  bottom: -($arrow-width - $border-size);
  left: $arrow-offsetX;
}

// 内容容器
.pop-message > .content {
  padding: 0.8em;
}

// Dropdown组件    
.dropdown-content {
  border-radius: 0.5em;
}

// toggle
.switch-wrap {
  font-size: 14px;
}





